import React from "react";
import { useStaticQuery, graphql, Link } from "gatsby";
import { useSiteMetadata } from "../../hooks/useSiteMetadata";
import Img from "gatsby-image"


// Styles
import "./HomeTestimonials.styles.scss";

// Components
import HomeTestimonialImage from "./HomeTestimonialImage.component";
import DividerArrow from "../DividerArrow/DividerArrow.component";

const HomeTestimonials = () => {

    const data = useStaticQuery(graphql`
        query {
            allMarkdownRemark(filter: {fileAbsolutePath: {regex: "/(testimonials)/"}}, limit: 1) {
                edges {
                    node {
                        frontmatter {
                            title
                            testimonialIntro
                            jobTitle
                            image
                            productImage {
                                childImageSharp {
                                    fluid {
                                        ...GatsbyImageSharpFluid
                                    }
                                }
                            }
                        }
                        html
                    }
                }
            }
        }
    `)
    

    return(
        <div className="home-testimonials">
            <DividerArrow />
            <h2>Testimonials</h2>
            {
                data.allMarkdownRemark.edges.map(({node}) => (
                    <div className="home-testimonials-container">
                        <Img fluid={ node.frontmatter.productImage.childImageSharp.fluid } />
                        <div className="home-testimonials-content">
                            <h3><em>{ node.frontmatter.testimonialIntro }</em></h3>
                            <h4>{ node.frontmatter.title }</h4>
                            <p className="sub-head">{ node.frontmatter.jobTitle }</p>
                            <div dangerouslySetInnerHTML={{ __html: node.html }} />
                            <Link to="/testimonials">Read More Testimonials</Link>
                        </div>
                    </div>
                ))
            }
        </div>
    )
}

export default HomeTestimonials;